.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.horizontal .wadi-tabs-nav-list .wadi-tab-link {
  background-color: rgba(128, 128, 128, 0);
  -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
  transition: transform 0.3s, background-color 0.3s;
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.horizontal .wadi-tabs-nav-list .wadi-tab-title {
  font-weight: 700;
  line-height: 1;
  margin: 0;
  color: #000;
  vertical-align: middle;
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.horizontal .wadi-tabs-nav-list a::after {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  content: "";
  background-color: inherit;
  -webkit-transition: -webkit-transform 0.3s, background-color 0.3s;
  transition: transform 0.3s, background-color 0.3s;
  -webkit-transform: perspective(900px) rotate3d(1, 0, 0, 90deg);
  transform: perspective(900px) rotate3d(1, 0, 0, 90deg);
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-perspective-origin: 50% 100%;
  perspective-origin: 50% 100%;
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.horizontal .wadi-tabs-nav-list li.tab-current a::after {
  -webkit-transform: perspective(900px) rotate3d(1, 0, 0, 0deg);
  transform: perspective(900px) rotate3d(1, 0, 0, 0deg);
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.vertical .wadi-tabs-nav-list .wadi-tab-link {
  background-color: rgba(128, 128, 128, 0);
