  transition: transform 0.3s, background-color 0.3s;
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.vertical .wadi-tabs-nav-list .wadi-tab-title {
