  transform: perspective(900px) rotateY(90deg);
  -webkit-transform-origin: 100% 0%;
  transform-origin: 100% 0%;
  -webkit-perspective-origin: 100% 0%;
  perspective-origin: 100% 0%;
}
.wadi-tabs-container .wadi-tabs-section .wadi-tabs.wadi-tabs-flip .wadi-tabs-nav.vertical .wadi-tabs-nav-list li.tab-current a::after {
  -webkit-transform: perspective(900px) rotateY(0deg);
